@import '../../globalVariable';

.page {
  height: 100%;
  background: #f3f3f3;
}

.authorizeContainer {
  background: #ffffff;
  max-width: 800px;
  margin: 32px auto;
  padding: 30px 48px;

  p {
    font-size: 14px;
    line-height: 20px;
    color: #616161;
  }

  .authorizeTitle {
    font-size: 20px;
    font-weight: bold;
    line-height: 28px;
    padding-bottom: 18px;
    border-bottom: 1px solid #D8D8D8;
  }

  .authorizeContent {
    display: flex;
    justify-content: space-between;
 
    .contentLeft {
      label {
        position: relative;
        display: inline-block;
        width: 100%;
        height: 28px;

        &::after {
          content: '';
          width: 37px;
          height: 30px;
          position: absolute;
          top: 37px;
          right: 0;
          border-left: 1px solid #e6e6e6;
          display: inline-block;
          background: url(../../assets/icon_arrow_bottom.png) no-repeat scroll center transparent;
          background-size: 13px;
        }
      }

      select {
        padding-left: 12px;
        width: 100%;
        position: relative;
        border: 1px solid #D9D9D9;
        border-radius: 4px;
        appearance: none;
        background: transparent;
        height: 40px;
        font-size: 14px;
      }

      p {
        margin-top: 10px;
      }
    }

    .contentRight {
      display: flex;
      margin: 28px 0 0 15px;

      .authorizeButton {
        @include buttonPubilc;
        background: #00B548;
      }

      .refuseButton {
        @include buttonPubilc;
        margin-left: 14px;
        color: #616161;
        background: #eaeaea;
      }
    }
  }

  .authorizeTips {
    background: #F7F7F7;
    padding: 20px 0;
    margin-top: 36px;

    h3 {
      font-size: 16px;
    }

    .opertionContent {
      display: flex;
      flex-direction: row;
      margin-bottom: 28px;
    }

    .checkDetail {
      margin: 0 30px 12px 30px;

      .thirdParty {
        text-decoration: none;
        color: #20c05c;
      }
    }

    .allowOperations {
      width: 50%;
      padding-left: 30px;

      p {
        @include operationPublic;
        background-image: url(../../assets/checkmark-lark.png);
        background-image: image-set(url(../../assets/checkmark-lark.png) 1x, url(../../assets/checkmark-lark@2x.png) 2x );
      }
    }

    .refuseOperations {
      width: 50%;
      border-left: 1px solid #D9D9D9;
      padding-left: 30px;

      p {
        @include operationPublic;
        background-image: url(../../assets/chacha.png);
        background-image: image-set(url(../../assets/chacha.png) 1x, url(../../assets/chacha@2x.png) 2x );
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .authorizeContainer {
    max-width: 400px;
    min-width: 320px;
    margin: 32px auto;
    padding: 28px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .authorizeTitle {
      width: 100%;
      text-align: center;
    }
    
    .authorizeContent {
      flex-direction: column;
      margin: 0 auto;

      .contentRight {
        flex-direction: column;
        margin-left: 0;

        .authorizeButton, .refuseButton {
          box-sizing: border-box;
          width: 100%;
          margin: 0 0 16px 0;
        }
      }
    }

    .authorizeTips {
      .opertionContent {
        display: flex;
        flex-direction: column;

        .allowOperations, .refuseOperations {
          box-sizing: border-box;
          width: 100%;
          border-left: none;
        }
      }
    }
  }
}