// scss 全局变量
$fontColor: #808083;
$smallestFontSize: 10px;
$smallFontSize: 12px;
$middleFontSize: 14px;
$bigFontSize: 16px;
$inputMarginBottom: 17px;
$yxLightGreen: #2DBE61;
$errorTipColor: #CD5450;

@mixin hide {
  &.hide {
    display: none;
  }
}

@mixin vercodeHelpLinkWrapper {
  .vercodeHelpLinkWrapper {
    margin: 20px 0 0;
    text-align: center;
    font-size: 13px;
    @include hide;
  }
}

@mixin wechatButtonWrapper {
  .wechatButtonWrapper {
    margin-top: 16px;
    border: 1px solid #cacaca;
    font-size: $smallFontSize;
    color: #848a8b;
    background: #fff;

    &:hover {
      background: #f8f8f8;
    }
  }
}

@mixin centerLayout {
  width: 380px;
  margin: 0 auto;
}

@mixin globalBorderTop {
  border-top: 4px solid $yxLightGreen;
}

@mixin publicBindButton {
  display: inline-block;
  box-sizing: border-box;
  height: 46px;
  line-height: 46px;
  padding: 0 20px;
}

@mixin operationPublic {
  padding-left: 22px;
  background-repeat: no-repeat;
  background-size: 20px;
  margin-bottom: 8px;
}

@mixin buttonPubilc {
  display: inline-block;
  padding: 0 25px;
  width: 100px;
  height: 46px;
  line-height: 46px;
  text-align: center;
  border: 0;
}